<script>
	import axios from 'axios';
	export default {
		data() {
			var validatePass = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''));
				} else {
					callback();
				}
			};
			var validatePass2 = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''));
				} else if (value !== this.numberValidateForm2.password2) {
					callback(new Error('!'));
				} else {
					callback();
				}
			};
			return {
				dialogFormVisible: false,
				dialogFormVisible2: false,
				dialogFormVisible3: false,
				dialogFormVisible4: false,
				dialogFormVisible5: false,
				numberValidateForm: {
					username: '',
					password: ''
				},
				numberValidateForm2: {
					username2: '',
					password2: '',
					realpassword: ''
				},
				data: [],
				rules: {
					username2: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							min: 5,
							max: 15,
							message: '5~15',
							trigger: 'blur'
						}
					],
					password2: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							min: 5,
							max: 15,
							message: '5~15',
							trigger: 'blur'
						},
						{
							validator: validatePass,
							trigger: 'blur'
						}
					],
					realpassword: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							min: 5,
							max: 15,
							message: '5~15',
							trigger: 'blur'
						},
						{
							validator: validatePass2,
							trigger: 'blur',
							required: true
						}
					]

				}
			};
		},
		methods: {
			submitForm(formName) {
				if (formName == 'numberValidateForm') {
					let formData2 = new FormData();
					formData2.append('key1', this.numberValidateForm.username);
					formData2.append('key2', this.numberValidateForm.password);
					const val = 'http://127.0.0.1:8090/login';
					axios.post(val, formData2, {
						headers: {
							'Content-Type': 'multipart/form-data'
						}
					}).then((result) => {
						if(result.data.data[0].state==1)
						alert("");
						else alert("")
					});
				} else {
					let formData = new FormData();
					formData.append('key1', this.numberValidateForm2.username2);
					formData.append('key2', this.numberValidateForm2.password2);
					formData.append('key3',this.numberValidateForm2.realpassword);
					const val = 'http://127.0.0.1:8090/regist';
					axios.post(val, formData, {
						headers: {
							'Content-Type': 'multipart/form-data'
						}
					}).then((result) => {
						let num=result.data.data;
						if(num==1)
						alert("");
						else if(num==0) alert("");
						else if(num==2) alert("");
						else if(num==3) alert("");
						else if(num==4) alert("");
						else alert("");
						console.log(result);
					});
				}
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			handleCancle() {
				this.$refs.form.resetFields()
				this.dialogVisible = false
			},
		}
	};
</script>